<template>
  <div class="container">
    <h1 class="display-4">{{ msg }}</h1>
    <div class="todos-section">
      <div class="add-todo">
        <div class="row">
          <div class="col-10">
            <input type="text" @keyup.enter="newTodo" v-model="todo" id="todo" class="form-control mb-3">
          </div>
          <div class="col-2">
            <button @click="newTodo" class="btn btn-primary">Add Todo</button>
          </div>
        </div>
      </div>

      <div class="todo" v-for="(item, index) in todos" :key="item._id">
        <input type="text" class="form-control updateText" v-model="editedTodo" v-if="isSelected(item)" />
        <p class="todo-text" v-else> <span class="badge badge-primary">{{ index + 1 }}</span> {{ item.todo }}</p>
        <button @click="isSelected(item) ? unselect() : select(item)" class="btn btn-success" >  {{ isSelected(item) ? 'close' : 'Edit' }} </button>
        <button @click="isSelected(item) ? updatedItem(item, index) : delTodo(item, index)" class="btn btn-danger"> {{ isSelected(item) ? 'save' : 'Delete' }}</button>
      </div>
      <button @click="clearAll" v-if="todos.length" class="btn btn-secondary"> clear all</button>
    </div>

  </div>
</template>

<script>
import { allTodos, addTodo, updateTodo, deleteTodo, deleteAllTodos } from "../TodoService";
export default {
  name: 'TodoComponent',
  props: {
    msg: String
  },
  data() {
    return {
      todos: [],
      error: '',
      todo: '',
      editedTodo: "",
      selected: {}
    }
  },
  async mounted() {
    try {
      this.todos = await allTodos();
    } catch (err) {
      this.error = err;
    }
  },

  methods: {
    // Create New Todo
    async newTodo() {

      try {
        const response = await addTodo({
        todo: this.todo
      });
        this.todos.push(response.data);
        this.todo = "";
      } catch (error) {
        console.log(error);
      }
    },

    select(item) {
      this.selected = item;
      this.editedTodo = item.todo;
    },

    unselect() {
      this.selected = {};
      this.editedTodo = ""
    },

    isSelected(item) {
      return item._id === this.selected._id
    },

    // update todo
    async updatedItem(item, i) {
      try {
        const response = await updateTodo(item, { todo : this.editedTodo });
        this.todos[i] = response.data;
        this.unselect()

      } catch (error) {
        console.log(error);
      }
    },

    // delete todo
    async delTodo(item, i) {
      try {
        await deleteTodo(item);
        this.todos.splice(i, 1)

      } catch (error) {
        console.log(error);
      }
    },

    // clear all
    async clearAll() {
      try {
        await deleteAllTodos()
        this.todos = await allTodos();
      } catch (error) {
        console.log(error);
      }
    }
  }
}
</script>

