import axios from 'axios';

export const allTodos = async () => {
    try {
        const response = await axios.get("http://localhost:5000/api/todos");
        const data = response.data;
        // return data.map(todo => ({
        //     todo,
        //     createdAt: new Date(todo.createdAt)
        // }))

        return data;

    } catch (err) {
        console.log(err.message);
    }
}

export const addTodo = async (todo) => {
    try {

        return await axios.post("http://localhost:5000/api/todos", todo );

    } catch (error) {
        console.log(error);
    }
}

export const updateTodo = async (item, updatedTodo) => {
    try {
        return await axios.patch(`http://localhost:5000/api/todos/${item._id}`, updatedTodo )
    } catch (error) {
        console.log(error);
    }
}

export const deleteTodo = async (item) => {
    try {
        return await axios.delete(`http://localhost:5000/api/todos/${item._id}`)
    } catch (error) {
        console.log(error);
    }
}

export const deleteAllTodos = async () => {
    try {
        return await axios.delete("http://localhost:5000/api/todos")
    } catch (error) {
        console.log(error);
    }
}